.key{
  width: 1604px;

  &__title{
    margin: 0 0 24px 0;
    font-family: 'Literata', sans-serif;
    font-size: 48px;
    line-height: 1.5;
    color: #fff;
    max-width: 590px;
    font-weight: 300;
  }

  &__span{
    color: #CA7E45;
    margin: 0;
  }
  
  &__list-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__subtitle{
    margin: 0 42px 0 0;
    font-family: 'SegoeUI', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: #fff;
    font-weight: 400;
    max-width: 528px;
  }

  &__list{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &__item{
    display: flex;
    justify-content: space-between;
    margin: 0 28px 0 0;
    align-items: center;
    &::before{
      height: 53.16px;
      width: 53.16px;
      content: url('../../images/safe.svg');
      margin: 0 16px 0 0;
    }

    &:last-of-type{
      margin: 0;
    }
  }

  &__item-text{
    margin: 0;
    font-family: 'SegoeUI', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: #fff;
    font-weight: 400;
    width: fit-content;
  } 
}

@media screen and(max-width: 1772px){
  .key{
    width: calc(100% - 168px);

    &__list-container{
      width: 100%;
      display: flex;
      flex-direction: column;
    }

    &__subtitle{
      margin: 0 0 36px 0;
    }

    &__item{
      width: fit-content;
    }
  }
}

@media screen and(max-width: 1360px){
  .key{
    width: 1224px;
  }
}

@media screen and(max-width: 1266px){
  .key{
    width: calc(100% - 32px);
  }
}

@media screen and(max-width: 960px){
  .key{

    &__list{
      flex-direction: column;
      align-items: flex-start;
    }

    &__item{
      margin: 0 0 32px 0;
    }
  }
}

@media screen and(max-width: 580px){
  .key{

    &__title{
      font-size: 32px;
      line-height: 1.5;
      max-width: 326px;
    }
  }
}